<template>
  <div id="mainRoot" class="hello">

    <lunbo></lunbo>
    <slider :showSideBar='show' :toggleSideBar='showOrHideMask'></slider>
    <div v-show='show' class="boxflex" @click='hide'></div>
    <div class="sortList" >
      <h3>人气推荐:</h3>
      <ul>
        <li class="fix" v-for="(it,i) in sortList" @click="go(i)">
          <span class="fl">{{i+1}}</span>
          <img :src="it.src" alt="" class="fl">
          <div class="fl">
            <h4>{{it.title}}</h4>
            <p><span class="colorF">{{it.moreR}}</span>人推荐，<span class="colorG">{{it.doCh}}</span>条动态</p>
          </div>
          <div class="fr"><i class="icon iconfont icon-jiahao2fill"></i></div>
        </li>
      </ul>
    </div>
    <div class="superPe" @click='hide'>
      <h3>推荐名人</h3>
      <ul class="fix">
        <li class="fl" v-for="it in superList">
          <img :src="it.src" alt="">
          <p>{{it.title}}</p>
        </li>
      </ul>
    </div>
    
    <div class="outLis">
      <ul class="listMain">
        <li @click='jump(0)'>
        <div class="fix fixTop"><p class="fl">热门推荐</p><span class="fr">查看全部</span></div>
        <div class="slideOl">  
          <ol class="fix fixOl">
            <li class="fl flI" v-for="hot in hotList">
              <img :src="hot.src" alt="">
              <p>{{hot.title}}</p>
            </li>
          </ol>
        </div>
        </li>

      </ul>
      <ul class="listMain">
        <li  @click='jump(1)'>
        <div class="fix fixTop"><p class="fl">企业推荐</p><span class="fr">查看全部</span></div>
          <div class="slideOl">  
          <ol class="fix fixOl" >
            <li class="fl flI" v-for="hot in hotList">
              <img :src="hot.src" alt="">
              <p>{{hot.title}}</p>
            </li>
          </ol>
        </div>
        </li>
      </ul>
      <ul class="listMain">
        <li @click='jump(2)'>
        <div class="fix fixTop"><p class="fl">服务推荐</p><span class="fr">查看全部</span></div>
          <div class="slideOl">  
          <ol class="fix fixOl" >
            <li class="fl flI" v-for="hot in hotList">
              <img :src="hot.src" alt="">
              <p>{{hot.title}}</p>
            </li>
          </ol>
        </div>
        </li>
      </ul>

    </div>
    <ul class='tabIndex'>
      <li :class="{key}" v-for="(item,key,i) in ulLinst" @click="getNum(key,i)">{{item}}</li>
    </ul>
    <component :is="current" class="tabcon" :lo="local"></component>
    <div style="height:4rem"></div>
    <!-- <mt-swipe :auto="4000">
      <mt-swipe-item>
        <img src="../../static/img/01.jpg" height="200" width="300" alt="">
      </mt-swipe-item>
      <mt-swipe-item><img src="../../static/img/02.jpg" height="200" width="300" alt=""></mt-swipe-item>
      <mt-swipe-item><img src="../../static/img/03.jpg" height="200" width="300" alt=""></mt-swipe-item>
    </mt-swipe> -->
  </div>
</template>

<script>

import lunbo from './lunbo.vue'
import slider from './slider.vue'
import {mapState,mapMutations,mapActions} from 'vuex'
export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to My Vue demo',
      //ulLinst:['第一版','第二版','第三版'],
      ulLinst:{first:'要闻',second:'热播',third:'话题'},
      current: 'first',
      local:'localmsg',
      show:false,
      second:false,
      third:false,
      hotList:[{
          src:'./static/img/a18.png',
          title:'天猫商城'
        },{
          src:'./static/img/a19.png',
          title:'聚划算'
        },{
          src:'./static/img/a20.png',
          title:'去哪儿'
        },{src:'./static/img/a19.png',
          title:'聚划算'
        },{
          src:'./static/img/a20.png',
          title:'去哪儿'
        },{src:'./static/img/a19.png',
          title:'聚划算'
        },{
          src:'./static/img/a20.png',
          title:'去哪儿'
        },{
          src:'./static/img/a21.jpg',
          title:'闲鱼'
        }],
       /*sortList:[{
          src:'./static/img/a18.png',
          title:'天猫商城',
          moreR:32123,
          doCh:2123
        },{
          src:'./static/img/a19.png',
          title:'聚划算',
          moreR:14523,
          doCh:1167
        },{
          src:'./static/img/a20.png',
          title:'去哪儿',
          moreR:11212,
          doCh:1054
        },{src:'./static/img/a19.png',
          title:'聚划算',
          moreR:8936,
          doCh:788
        }],*/
        superList:[
        {
          src:'./static/img/05.jpg',
          title:'闲鱼1号'
        },{
          src:'./static/img/06.jpg',
          title:'闲鱼2号'
        },{
          src:'./static/img/07.jpg',
          title:'闲鱼3号'
        }
        ] 
    }
  },
  props:['thisSow'],
  created:function(){

  },
  computed:{
    sortList(){
      return this.$store.state.sortList;

    }
  },
  created:function(){
    //var el='';
      //localStorage.setItem('el',JSON.stringify(this.sortList))
  },
  components:{lunbo,slider,
    first:{template:`<div style="">
      <p>李克强会上谈到自家冰箱意在这件大事</p>
      <p>杜特尔特声称将命军队占领南海争议岛礁 中方回应</p>
      <p>许勤任河北省副省长、代理省长(图/简历)</p>
      <p>辽宁人大常委会原副主任王阳涉贿选案被起诉</p>
      <p>江苏原省委常委、常务副省长李云峰被双开</p>
      <p>李克强会上谈到自家冰箱意在这件大事</p>
    </div>`},
    second:{template:`<div>
    <p>张一山开嘲讽模式</p>
      <p>5分钟记三千张图 中方回应</p>
      <p>JK变超萌闹钟(图/简历)</p>
      <p>见字如面见字如面见字如面</p>
      <p>4女1男藏果园吸毒 其中一人刚生完孩子</p>
      <p>《人民的名义》在抹黑警察？编剧：这是主观误读</p>
    </div>`},
    third:{template: `<div>
      <p>《人民的名义》热播，为啥达康书记圈粉无数?</p>
      <p>当全职妈妈没追求？那只是经济决策！ 中方回应</p>
      <p>舆论不是司法的“假想敌”(图/简历)</p>
      <p>A股、美股、港股有啥不同？</p>
      <p>4女1男藏果园吸毒 其中一人刚生完孩子</p>
      <p>赵成根：行政智慧以致于为之骄傲的就是官僚制</p>
      </div>`}},
  methods:{
    showOrHideMask(show){
        this.show=show;
      },
    hide(){
        this.show=!this.show;
      },
    getNum:function(e,i){
      this.current = e;
          $('.tabIndex li').eq(i).addClass('activeLi').siblings().removeClass('activeLi')
    },
    jump:function(n){
      this.$router.push({path:'/hiO',query:{id:n}});
    },
    go:function(n){
      this.$router.push({path:'/sort',query:{id:n}});
    },
    ...mapActions([
      'setTop'
    ])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.hello{font-size: 0.7rem}
h1{font-size:1.2rem;text-align: center;height:2rem;line-height: 2rem;}
.listMain{width:100%;padding:0 1rem;box-sizing:border-box;border-bottom:1px solid #dbdbdb;}
.listMain>li{width:100%;height:4.8rem;margin-top:0.3rem;padding-bottom:0.5rem;overflow: hidden;}
a {
  color: #42b983;
}
.tabIndex .activeLi{background:#3399cc;color:#fff;}
.colorF{color:#f40;}
.colorG{color:#f10;}
.sortList{padding: 1rem 0;}
.sortList h3,.superPe h3{text-align: left;padding-bottom:.3rem;text-indent: 1em;border-bottom: 1px solid #ccc;}
.sortList ul li{
  border-bottom: 1px solid #ccc;
  
  padding:0.7rem 0 0.5rem;
}
.boxflex{width:100%;height:100%;position: fixed;left:0;top:0;background: rgba(0,0,0,0.4);z-index: 110;}
.sortList .fr{width:3.6rem;padding:0.25rem 0;}
.sortList .icon-jiahao2fill{
  color:#f50;
  font-size: 1.5rem;
}
.sortList ul li span.fl{
  display: inline-block;
  width:2rem;
  height:2rem;
  line-height: 2rem;
  color:#f40;
  font-style: italic;
  font-size: 1rem;

}
.sortList ul li div.fl{
  text-align: left;
  margin-left:0.6rem;
  height:2rem;
  line-height: 1.12rem;
  
}
.sortList ul li h4{

  
}
.sortList img{
  width:2rem;
  height:2rem;
 
}
.superPe{border-bottom:1px solid #ccc;padding-bottom:0.6rem;margin-bottom:0.6rem;}
.superPe ul{width:90%;margin:0 auto;}
.superPe h3{border:none;padding-bottom:0.6rem;}
.superPe li{width:33.333%;}
.superPe li img{width:3rem;height:3rem;border-radius: 50%;}
.tabIndex{
  overflow: hidden;
  zoom:1;
  list-style: none;


}
.hello .tabcon p{width:18rem;margin:0 auto;text-align: left;text-indent: 1em;font-size: 0.8rem;height:1.8rem;line-height: 1.8rem;border-bottom:1px solid #ccc;display:block;white-space:nowrap; overflow:hidden; text-overflow:ellipsis;
}
.tabIndex li{
  float:left;

  width:33.333%;
  height:1.6rem;
  line-height: 1.6rem;
  border:1px solid #ccc;
  box-sizing:border-box;
  color:#000;
}
.slideOl{
  position: relative;
  overflow-x: auto;
  height:5rem;
}
.fixOl{
  height:100%;
  width:24rem;
  overflow: hidden;
  left:0;
  top:0;

}
.flI{
  margin-right:0.6rem;
}
.flI img{
  
  width:2rem;
  height:2rem;

}
.fixTop{padding:0.3rem 0;}
.fixTop p{font-weight: bold;}
</style>



