<style>
    .vsidebar{
      position: fixed;
      top:0;
      left:-8rem;
      transition: all 0.3s ease;
      bottom:0;
      width:8rem;
      background-color:#fff;
      z-index:150;
      display: flex;
      flex-direction: column;
      font-weight: 500;}
      .vsidebar .login{
        height:9rem;
        min-height:9rem;
        display: flex;
        background-color: #03A9F4;}
        .vsidebar .login div{
          flex:1;
          height:100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          padding:2rem;}
        .vsidebar .login  img{
            height:4rem;
            border-radius:50%;
            background-color: red;
            width:4rem;
          }
        .vsidebar .login  span{
            height:3rem;
            line-height: 3rem;
            width:80%;
            color:yellow;
            text-align: center;
          }


    .vsidebar  .about{
        border-top: 1px solid #ccc;
       padding-top:1rem;


        display: flex;
        align-items: center;
        color:black;
        font-weight: bold;}
     .vsidebar .about span i{

            padding: 0 1rem;

      }
      .topics{
        
        display:flex;
        align-items: center;}
      .topics ul{
          color:black;
          text-align:left;
          width:100%;
          font-weight: bold;}
      .topics ul li{
            padding:0.6rem 0;
            border-bottom:1px solid #ccc;

          }
       .topics ul li i{
              padding: 0 1rem;
            }
    .show{
      transform:translateX(8rem);
    }
</style>


