<template>
	<div id="hiW">
		<ul class="hiImg">
			<li v-for='item in resArry' @click="getInit">
			<router-link :to="{path:'/price',query:{id:item}}">
			<div class="head">
				<img src="../../static/img/05.jpg"class="fl">
				<span class="fl">我的最爱</span>
				<span class="fr">价格：<span style="color:#f40">{{item.price}}</span></span>
			</div>
			<img class="imgCLas" :src='item.src' height="200" width="300" alt="">
					<!-- <p class="fix"><span >价格：{{item.price}}</span>
					<mt-button  type="primary">购买</mt-button>
					</p> -->
			<p class="pcls">简介：移动 App 市场研究机构 Newzoo 最近发布的一份报告显示，在中国境内监测到的 3.5 亿台常用移动设备里，包括手机和平板电脑，iOS 系统的份额从今年 1 月的 29.1% 下降到 26.3%。
尽管苹果还保持着市场第一的地位，但在后面追赶的 Android 手机厂商增长势头还很明显。</p>
			</router-link>
			</li>
		</ul>
	</div>
</template>
<script>
import hi from './hi.vue'
import price from './price.vue'
import {mapActions} from 'vuex'
	export default {
		data(){
			return{
				resArry:[],
			}
		},
		created:function(){
			var that = this;
			that.$http.get('./static/test.json').then(function(res){

				that.resArry=res.data.data;

				//that.$set(this.resArry,res.data.data)
				console.log(this.resArry)
			},function(err){

			})			
		},
		methods:{
			gotoPrice:function(){
				/*this.$router.go(-1);*/
				this.$router.push('/price');
			},
			...mapActions([
				'getInit'
				])
		}
	}
</script>
<style>
.hiImg{font-size: 0.9rem;color:#000;}
.hiImg a{color:#000;}
.head{padding:0.6rem 0.4rem;margin-bottom:0.3rem;}
.head span{margin-top:0.5rem;}
.head img{width:2rem;height:2rem;border-radius: 50%;vertical-align: middle;border:1px solid #ccc;}
.hiImg li{margin:10px auto;}
.hiImg .mint-button{height:1.6rem;line-height: 1.6rem;}
.hiImg .imgCLas{width:90%;height:7rem;margin:0.6rem auto;}
.hiImg .pcls{text-align: left;color:#000;padding:0 0.3rem;font-size: 0.8rem;line-height: 1.2rem;text-indent: 1em;}
</style>


