<template>
	<div id="animate">

    	<mt-header fixed :title="title">
		  <router-link to="/" slot="left">
		    <mt-button icon="back">返回</mt-button>
		    <mt-button>首页</mt-button>
		  </router-link>
  
</mt-header>
		<h2 style="height:2rem"></h2>
 		<img class="animated zoomIn" src="../../static/img/a19.png" height="87" width="87" alt="">
    	<h3>vue 的详情页面内容</h3>
		<img class="animated rotateIn" src="../../static/img/a20.png" height="87" width="87" alt="">
		<h3>vue 的详情页面内容</h3>
		<img class="animateImg" src="../../static/img/a20.png" height="87" width="87" alt="">
		<h3>vue 的详情页面内容</h3>
		<div class="out">
			<img class="transformed" src="../../static/img/a20.png" height="87" width="87" alt="">
		</div>
	</div>
</template>
	<script>
		export default{
			data(){
				return{
					title:this.$route.query.title
				}
			}
		}	
	</script>
	<style>
#animate img{
	padding:0.6rem 0;
}
/* .transformed{
	animation: myfirstl infinite;
	-moz-animation: myfirstl infinite;	Firefox
	-webkit-animation: myfirstl infinite;	Safari 和 Chrome
	-o-animation: myfirstl infinite;	Opera
	}
@keyframes myfirstl{
	0%{transform:rotate(0deg);}
	100%{transform:rotate(360deg);}
} */
.out{
	transition:all 2s infinite;
	-moz-animation: all 2s infinite;
	-webkit-animation: all 2s infinite;
	-o-animation: all 2s infinite;
}
.transformed{
	transform:rotate(60deg);
	-moz-transform:rotate(60deg);
	-webkit-transform:rotate(60deg);
	-o-transform:rotate(60deg);
}
.animateImg
{
animation: myfirst 5s;
-moz-animation: myfirst 5s;	/* Firefox */
-webkit-animation: myfirst 5s;	/* Safari 和 Chrome */
-o-animation: myfirst 5s;	/* Opera */
}
/* @keyframes myfirst
{
from {background: red;}
to {background: yellow;}
}

@-moz-keyframes myfirst Firefox
{
from {background: red;}
to {background: yellow;}
}

@-webkit-keyframes myfirst Safari 和 Chrome
{
from {background: red;}
to {background: yellow;}
}

@-o-keyframes myfirst Opera
{
from {background: red;}
to {background: yellow;}
} */
@keyframes myfirst
{
0%   {background: red;}
25%  {background: yellow;}
50%  {background: blue;}
100% {background: green;}
}

@-moz-keyframes myfirst /* Firefox */
{
0%   {background: red;}
25%  {background: yellow;}
50%  {background: blue;}
100% {background: green;}
}

@-webkit-keyframes myfirst /* Safari 和 Chrome */
{
0%   {background: red;}
25%  {background: yellow;}
50%  {background: blue;}
100% {background: green;}
}

@-o-keyframes myfirst /* Opera */
{
0%   {background: red;}
25%  {background: yellow;}
50%  {background: blue;}
100% {background: green;}
}
	</style>}



