import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import hiO from '@/components/hiO'
import hiT from '@/components/hiT'
import hiW from '@/components/hiW'
import hiF from '@/components/hiF'
import sort from '@/components/sort'
import price from '@/components/price'
import animate from '@/components/animate'
Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component:index
    },
    {
      path: "/",
      redirect:"/index",
      component:index
    },
    {
      path: '/hiO',
      name: 'hiO',
      component: hiO
    },
    {
      path: '/hiT',
      name: 'hiT',
      component: hiT
    },
    {
      path: '/hiW',
      name: 'hiW',
      component: hiW
    },
    {
      path: '/hiF',
      name: 'hiF',
      component: hiF
    },
    {
      path: '/age',
      name: 'age',
      component: hiW
    }
    ,
    {
      path: '/animate',
      name: 'animate',
      component: animate
    },
    {
      path: '/price',
      name: 'price',
      component: price
    },
    {
      path: '/sort',
      name: 'sort',
      component: sort
    }
  ]
})



