<template>
	<div id="hiT">
		<img src="../../static/img/06.jpg" alt="">
		<h2>hi  欢迎你!</h2>
		<p>  本demo采用vue-cli构建项目，综合运用vue2.0,vuex,vue-router,vue-resource,zeptojs,
		swiperjs,mintUI,animate.css,iconfont框架和技术布局而成！欢迎查看，提出修改意见！</p>
		
		<a href="https://cn.vuejs.org/v2/guide/" title="">
		<mt-button size="small">vue2.0</mt-button>
		</a>
		<a href="https://vuex.vuejs.org/zh-cn/" title="">
		<mt-button size="small">vuex</mt-button>
		</a>
		<a href="https://github.com/madrobby/zepto" title="">
		<mt-button size="small">zepto</mt-button>
		</a>
		<a href="http://www.swiper.com.cn/" title="">
		<mt-button size="small">swiper</mt-button>
		</a>
		<a href="https://vuefe.cn/v2/guide/routing.html" title="">
		<mt-button size="small">vue-router</mt-button>
		</a>
		<a href="https://github.com/pagekit/vue-resource" title="">
		<mt-button size="small">vue-resource</mt-button>
		</a>
		<a href="https://daneden.github.io/animate.css/" title="">
		<mt-button size="small">animate.css</mt-button>
		</a>
		<a href="http://mint-ui.github.io/docs/#!/zh-cn2" title="">
		<mt-button size="small">mintUI</mt-button>
		</a>
		<a href="http://www.w3cplus.com/sassguide/" title="">
		<mt-button size="small">sass</mt-button>
		</a>
		<a href="http://www.iconfont.cn/" title="">
		<mt-button size="small">iconfont</mt-button>
		</a>
		
<mt-button type="primary" @click.native="openConfirm">修改</mt-button>
<mt-button @click.native="openConfirm2" type="danger">意见</mt-button>
	</div>
</template>
<script>
import { MessageBox } from 'mint-ui';
	export default {
		data(){
			return{
				title2:''
			}
		},
		props:['title'],
		methods:{
			goBack:function(){
				/*this.$router.go(-1);*/
				this.$router.push('/');
			},
		openConfirm() {
			//MessageBox.prompt('请输入姓名').then(({ value, action }) => {alert(value,action)});
      		MessageBox.confirm('确定执行此操作?').then(action => {
      			alert(1)
      		},err =>{
      			alert(2)
      		});
      		},
		
		openConfirm2() {
			//MessageBox.prompt('请输入姓名').then(({ value, action }) => {alert(value,action)});
      		MessageBox.confirm('确定执行此操作?').then(action => {
      			alert(3)
      		},err =>{
      			alert(4)
      		});
      		}
		}
	}
</script>
<style>
#hiT{
	width:94%;
	margin:0 auto;
	padding:0 0 4rem 0;
	text-align:left;}
#hiT img{display:block;width:5rem;height: 5rem;border-radius: 50%;margin:1rem auto 0.2rem;}
#hiT h2{font-size: 0.9rem;}
#hiT p{font-size: 0.8rem;line-height: 1.2rem;}
#hiT button{margin:5px 10px;}

</style>


