<template>
  <div id="index">
  <component :is="currentView"></component>
  <ul class="hi">
      <li :class="{active:mainRoot}" @click="getComponent('mainRoot','首页')">
      <i class="icon iconfont icon-home"></i>
      <p>首页</p>
      </li>
      <li :class="{active:hiT}" @click="getComponent('hiT','信息')">
      <i class="icon iconfont icon-wodefankui"></i>
      <p>信息</p>
      </li>
      <li :class="{active:hiW}" @click="getComponent('hiW','定制')">
      <i class="icon iconfont icon-wode"></i>
      <p>定制</p>
     </li>
      <li :class="{active:hiF}" @click="getComponent('hiF','设置')">
      <i class="icon iconfont icon-setting"></i>
      <p>设置</p>
      </li>
    </ul>
  </div>
</template>

<script>
import {mapMutations,mapActions} from 'vuex'
import mainRoot from './mainRoot'
import hiT from './hiT.vue'
import hiW from './hiW.vue'
import hiF from './hiF.vue'
export default {
  name: 'index',
  data () {
    return {
                title: "首页",
                currentView:mainRoot,
                mainRoot:true,
                hiT:false,
                hiW:false,
                hiF:false
            }
  },
  components:{
    mainRoot,
    hiT,
    hiW,
    hiF
},
  methods:{
          getComponent:function(url,title){
            this.currentView=url;
            this.title=title;
            this.mainRoot=false;
            this.hiT=false;
            this.hiW=false;
            this.hiF=false;
            switch(url){
              case "mainRoot":this.mainRoot=true;break;
              case "hiT":this.hiT=true;break;
              case "hiW":this.hiW=true;break;
              case "hiF":this.hiF=true;break;
          }
        },
    ...mapMutations([
      'jumptitle'
    ]),
    ...mapActions([
      'getInit'
    ]),

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{margin:0;padding:0;}
.hello{font-size: 0.7rem}
h1{font-size:1.2rem;text-align: center;border:1px solid #ccc;height:2rem;line-height: 2rem;}

#index .hi{
    position: fixed;
    left:0;
    bottom:0;
    width:100%;
    height:2.8rem;
    background:#dbdbdb;
    list-style: none;

  }
  .hi{color:#000;text-decoration: none;width:100%;height:100%;}
  .hi p{font-size: 0.8rem;margin-top:0rem;}
  .hi .router-link-active{color:#f40;}
  .hi i.iconfont{font-size: 1rem;}
  .hi li{float:left;width:25%;line-height:1rem;margin-top:0.4rem;}
  .active i,.active p{color:red;}

</style>



