<template>
	<div id="hiO">
 		
    	<mt-header fixed :title="titi">
		  <router-link to="/" slot="left">
		    <mt-button icon="back">返回</mt-button>
		    <mt-button>首页</mt-button>
		  </router-link>
  
</mt-header>
<div style="height:3rem"></div>
<div class="fix confix" v-for='item in hioList' @click="jumpC">
	<img :src="item.imgSur" alt="" class="fl">
	<P>
		<h4>{{item.imgH}}</h4>
		<span>{{item.imgP}}</span>
	</P>
</div>
<div class="sortList">
      <ul>
        <li class="fix" v-for="(it,i) in sortList">
          <span class="fl">{{i+1}}</span>
          <img :src="it.src" alt="" class="fl">
          <div class="fl">
            <h4>{{it.title}}</h4>
            <p><span class="colorF">{{it.moreR}}</span>人推荐，<span class="colorG">{{it.doCh}}</span>条动态</p>
          </div>
          <div class="fr"><i class="icon iconfont icon-jiahao2fill"></i></div>
        </li>
      </ul>
    </div>
		
	</div>
</template>
<script>

	export default {
		data(){
			return{
				titi:'',
				hioList:[{
					imgSur:'./static/img/a18.png',
					imgH:'天猫超市',
					imgP:'购物超市--互联网时代的伟大实现',
					imgId:'0'
				},{
					imgSur:'./static/img/a19.png',
					imgH:'天猫超市',
					imgP:'购物超市--互联网时代的伟大实现',
					imgId:'1'
				},{
					imgSur:'./static/img/a20.png',
					imgH:'天猫超市',
					imgP:'购物超市--互联网时代的伟大实现',
					imgId:'2'
				}]

			}
		},
		mounted:function(){
			switch(this.$route.query.id){
				case 0: this.titi = "热门推荐";break;
				case 1: this.titi = "企业推荐";break;
				case 2: this.titi = "服务推荐";break;
			}
			
		},
		methods:{
			goBack:function(){
				this.$router.push('/');
			},
			jumpC:function(){
				this.$router.push({path:'/animate',query:{title:this.titi}});
			}
		}
	}
</script>
<style>
*{margin:0;padding:0;}
h2{padding-top:2rem;}
.confix{padding:0.2rem;border-bottom:1px solid #dbdbdb;}
.confix img{width:2.5rem;height:2.5rem;margin-right:0.4rem;}
.confix h4{text-align:left;font-size: 0.8rem;padding-top:0.3rem;margin-bottom:0.4rem;}
.confix span{display:block;font-size: 0.6rem;text-align:left;}
</style>


