
import Vue from 'vue'
import Vuex from 'vuex'
import VueResourse from 'vue-resource'
Vue.use(VueResourse)
Vue.use(Vuex)
const state = {
	count:34,
	price:0.1,
	topTitle:'',
	jumpList:[
		'热门推荐','企业推荐','服务推荐'
	],
	resArry:[],
	sortList:[{
          src:'./static/img/a18.png',
          title:'天猫商城',
          moreR:32123,
          doCh:2123
        },{
          src:'./static/img/a19.png',
          title:'聚划算',
          moreR:14523,
          doCh:1167
        },{
          src:'./static/img/a20.png',
          title:'去哪儿',
          moreR:11212,
          doCh:1054
        },{src:'./static/img/a19.png',
          title:'聚划算',
          moreR:8936,
          doCh:788
        }]
}
const mutations ={
	add(state){
		state.count++
	},
	reduce(state){

		state.count--
	},
	setToptitle(state,e){
		state.count=e
	},
	getIn(state){
		
			console.log(3)	
	},
	addZ(state,n){
		state.sortList[n].moreR++
	},
	jumptitle(state,n){
		switch(n){
			case 0:return msn = state.jumpList[0];break;
			case 1:return msn =state.jumpList[1];break;
			case 2:return msn =state.jumpList[2];break;
		}
	}
}

const getters={
	counts:function(state){
		return state.price=(state.count*0.1).toFixed(1)
	}
}

const actions={
	addp:function(context){
		context.commit('add')
	},
	setTop:function(context){
		context.commit('setToptitle')
	},
	setRedu:function(context){
		context.commit('reduce')
	},
	getInit:function(context){
		context.commit('getIn')
	},
	addZan:function(context,n){
		context.commit('addZ',n)
	}
}

export default new Vuex.Store({
	state,
	mutations,
	getters,
	actions
})

















