        },{
          src:'./static/img/02.jpg',
          title:'two'
        },{
          src:'./static/img/03.jpg',
          title:'third'
        },{
          src:'./static/img/04.jpg',
          title:'four'
        }]
      } 
    },
    methods:{
      jump:function(title){
          
      }
    },
    mounted() {
     var mySwiper = new Swiper ('.swiper-container', {
     loop: true,
     autoplay:2000,
    // 如果需要分页器
    pagination: '.swiper-pagination',
  })        
    }
  }
</script>
<style>
	 @import '../../static/css/swiper.min.css';
  .swiper-pagination-bullet-active {
    background: #fff;
  }
  .swiper-container{
      width:100%;
      height:auto;
      overflow: hidden;
    }
  .swiper-container img{
      width:100%;
      height:9rem;
      overflow: hidden;
      display: block;
    }
</style>


