<template>
	<div id="price">
		<div style="height:3rem;"></div>
		<mt-header fixed title="购买">
		  <router-link to="/" slot="left">
		    <mt-button icon="back">返回</mt-button>
		    
		  </router-link>
		  
		</mt-header>
		<div class="cls">
			<img :src="src" alt="">
		  	<p class="fix" style="border-bottom:1px solid #ccc;">单价：<span style="float:right;margin-right:20px;">{{itemPrice}}</span></p>
			<div class="fix" style="border-bottom:1px solid #ccc;line-height:1.9rem;"><span style="">数量：</span>
				<p class="fr" style="float:right;margin-right:20px;vertical-align:middle;">
				<mt-button  size="small" @click="setRedu">-</mt-button>
				<span>{{count}}</span>
				<mt-button size="small" @click="addp">+</mt-button>
				<!-- <span class="asd" @click="addp">+</span> -->
				
				</p>
			</div>
			<p>总额：{{counPrice}}</p>
		  </div>
		  <div class="page-popup-wrapper">
      
      <mt-button @click.native="popupVisible2 = true" size="large">购买</mt-button>
      
    </div>
   
    <mt-popup v-model="popupVisible2" position="top" class="mint-popup-2" :modal="false">
      <p>购买成功</p>
    </mt-popup>
    
	</div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
	export default{
		data(){
			return{
				popupVisible2: false,
				itemPrice:this.$route.query.id.price,
				src:this.$route.query.id.src

			}

		},
		watch: {
			popupVisible2(val) {
				if (val) {
					setTimeout(() => {
						this.popupVisible2 = false;
					}, 2000);
				}
			}
		},
		computed:{
			count(){
				return this.$store.state.count
			},
			counPrice(){
				return (this.itemPrice*this.count).toFixed(1)
			}
		},
		methods:{...mapActions([
			'addp','setRedu'
			])
		
		}
	}
</script>
<style>
.cls{text-align:left;padding:0 0.5rem;}
	.cls img{width:90%;height:8rem;margin:1rem auto;}
.cls p{height:2rem;line-height: 2rem;}
.asd,.areduce{width:1rem;height:1rem;display: inline-block;line-height: 1rem;border:1px solid #ccc;text-align: center;margin:0 5px;}
#price .mint-popup-2 {
        width: 100%;
        height: 50px;
        text-align: center;
        background-color: rgba(0,0,0,.7);
        backface-visibility: hidden;
      }

      .mint-popup-2 p {
        line-height: 50px;
        color: #fff;
      }
</style>



